.slope {
	background: #CADC88;
	width: 100%;
	height: 100px;
	border-radius: 50% 50% 0px 0px / 100% 100% 0px 0px;
	overflow: hidden;
    z-index: -1;
}
.slope:before {
    content: "";
    background: #F2EDB4;
    border-radius: 50%;
    width:80px;
    height: 30px;
    position: absolute;
    left:50%;
    margin-left: -40px;
    top:-5px;   
    
    
}
.background {
    width: 350px;
    height: 445px;
    position: absolute;
    margin: auto;
    bottom: 0;
    left: -200px;
    right: -200px;
    
}
.background .slope {
    position: absolute;
    bottom:0;
}
