.tree {
/* 			background: ; */
    width:2px;
/* 			height: 200px; */
    border-right: 8px solid transparent;
    border-left: 8px solid transparent;
    border-bottom: 300px solid #816954;
    position: absolute;
    left: 50%;
    opacity: 0.6;
    transform-origin: 50% 300px;
    top: 500px;
    margin-left: -8px;
}


.branch {
    position: absolute;
    width: 150px;
    height: 40px;
    border-bottom: 5px solid #816954;			
}
.branch:before, .branch:after {
    content: "";
    position: absolute;
    width: inherit;
    height: inherit;
    border:inherit;
    border-radius: inherit;
    z-index: 0;
}
.branch:before {
    transform: rotate(1deg);
}
.branch:after {
    transform: rotate(2deg);
}
.branch.left, .branch.left:before, .branch.left:after {
    transform-origin: 0px 0px;
}
.branch.right, .branch.right:before, .branch.right:after {
    transform-origin: 100% 0px;
}
.branch.left {
    border-bottom-left-radius: 100% 100%;
    transform-origin: 100% 100%;
}
.branch.right {
    border-bottom-right-radius: 100% 100%;
    transform-origin: 0% 100%;
    margin-left: 1px;
}

.tree>.branch.left {
    transform:rotate(5deg);
}
.tree>.branch.right {
    transform:rotate(-5deg);
}
.leaf { 
    position: absolute;
    width: 23px;
    height: 23px;
    background: #C5EABE; 
    border-radius: 0px 100%;
    z-index: 1;
    transform-origin: 0px 0px;
    
}
.leaf:before {
/*    content:"";*/
    position: absolute;
    width:2px;
    height: 2px;
    background: #f55;
}
.flower {
    position: absolute;
    width: 50px;
    height: 50px;
    z-index: 2;
}
.flower:after {
    position: absolute;
    content: "";
    left: 0px;
    right: 0px;
    top: 0px;
    bottom: 0px;
    background: #e55;
    border-radius: 50%;
    width: 30%;
    height: 30%;
    margin: auto;
}
.flower .petal {
    width: 100%;
    height: 100%;
    position: absolute;
    transform: rotate(72deg);
}
.flower .petal::before, .flower .petal::after {
    content: "";
    position: absolute;
    width: 60%;
    height: 60%;
    background: #F0A5A6;
    border-radius: 0px 100px;
    top: 50%;
    left: 50%;
    margin-left: -20%;
    margin-top: -20%;
    transform-origin: 33% 33%;
}
.flower .petal::after {
    transform: rotate(36deg)
}

.flower.petal5 .petal {
    transform: rotate(144deg);
}
.flower.petal5 .petal::after {
    transform: rotate(-72deg);   
}
.flower.petal5>.petal::after {
    content:none;
}
.flower.petal6 .petal{
    transform: rotate(120deg);
}
.flower.petal6 .petal::after {
    transform: rotate(60deg);   
}
.flower.petal8 .petal{
    transform: rotate(90deg);
}
.flower.petal8 .petal::after {
    transform: rotate(45deg);   
    width: 56%;
    height: 56%;
}


.flower.roundpetal .petal:before, .flower.roundpetal .petal:after{
    width: 30%;
    border-radius: 0px;
    margin-left: -15%;
    margin-top: 0%;
    top: 0%;
    height: 50%;
    border-radius: 50% 50% 0px 0px;
    transform-origin: 50% 100%;
}
.heart {
    position:absolute;
    width: 50px;
    height: 50px;
    z-index: 2;
}
.heart:before, .heart:after {
    position: absolute;
    content: "";
    width:80%;
    height: 80%;
    border-radius: 100% 0%;
    background: #F0A5A6;
    left: 50%;
    top: 20%;
    transform-origin: 0% 100%;
}
.heart:before {
    transform: rotate(-65deg);
}
.heart:after {
    transform: rotate(-25deg);
}



.tulip {
    position:absolute;
    width: 50px;
    height: 50px;
    z-index: 2;
    transform-origin: 50% 100%;
}
.tulip:before, .tulip:after, .tulip .peak {
    position: absolute;
    content: "";
    width:80%;
    height: 80%;
    border-radius: 100% 0%;
    background: #F0A5A6;
    left: 50%;
    top: 20%;
    transform-origin: 0% 100%;
}
.tulip:before {
    transform: rotate(-70deg);
}
.tulip:after {
    transform: rotate(-20deg);
}
.tulip .peak {
    transform: rotate(-45deg);
}


.redflower:before, .redflower:after, .redflower .peak , .redflower .petal:before, .redflower .petal:after {
    background: #F0A5A6;
}

.greenflower:before, .greenflower:after, .greenflower .peak , .greenflower .petal:before, .greenflower .petal:after {
    background: #D5E4B6;
}
.blueflower:before, .blueflower:after, .blueflower .peak , .blueflower .petal:before, .blueflower .petal:after {
