}
.purpleflower:before, .purpleflower:after, .purpleflower .peak , .purpleflower .petal:before, .purpleflower .petal:after {
    background: #F0CCFE;
}

.flower:after {
    background: #f5f5f5;
}


.branch .branch:after {
    content:none;
}


